.modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.modal_content {
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  position: relative;
  width: min(900px, 92vw);

  img {
    width: 300px;
  }
}

.closeBtn {
  padding: 8px 16px;
  background: #333;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;

  &:hover {
    background: #740191;
  }
}