.kurlyDetail {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  background: #ffffff;
  color: #111827;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 18px 18px 88px;
  box-sizing: border-box;
}
.kurlyDetail * {
  box-sizing: border-box;
}

.productDetailWrap {
  display: grid;
  grid-template-columns: 420px 1fr;
  gap: 32px;
  align-items: start;
}

.imageBox {
  top: 24px;
}
.imageBox .image {
  width: 100%;
  border-radius: 12px;
  -o-object-fit: cover;
     object-fit: cover;
  display: block;
}

.pdHeader .pdCategory {
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 0 0 10px;
  font-size: 12px;
  color: #6b7280;
}
.pdHeader .pdCategory .sep {
  opacity: 0.6;
}
.pdHeader .pdTitle {
  margin: 0 0 6px;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: -0.3px;
}
.pdHeader .pdSub {
  margin: 0 0 14px;
  font-size: 13px;
  color: #6b7280;
}

.priceBlock {
  padding: 14px 0 16px;
  border-top: 1px solid #e5e7eb;
  border-bottom: 1px solid #e5e7eb;
}
.priceBlock .priceRow {
  display: flex;
  align-items: baseline;
  gap: 10px;
  margin-bottom: 10px;
}
.priceBlock .priceRow .badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 22px;
  padding: 0 8px;
  border-radius: 999px;
  background: rgba(95, 0, 128, 0.12);
  color: #5f0080;
  font-weight: 700;
  font-size: 12px;
}
.priceBlock .priceRow .sale {
  font-size: 24px;
  font-weight: 800;
  letter-spacing: -0.4px;
}
.priceBlock .priceRow .origin {
  color: #9ca3af;
  font-size: 13px;
}
.priceBlock .couponRow {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
}
.priceBlock .couponRow .badge.outline {
  background: transparent;
  border: 1px solid rgba(95, 0, 128, 0.35);
  color: #5f0080;
}
.priceBlock .couponRow .coupon {
  font-size: 18px;
  font-weight: 800;
}
.priceBlock .couponRow .hint {
  font-size: 12px;
  color: #6b7280;
}
.priceBlock .couponRow .couponBtn {
  margin-left: auto;
  height: 34px;
  padding: 0 12px;
  border-radius: 8px;
  border: 1px solid rgba(95, 0, 128, 0.35);
  background: #fff;
  color: #5f0080;
  font-weight: 700;
  cursor: pointer;
}
.priceBlock .couponRow .couponBtn:hover {
  border-color: #5f0080;
}
.priceBlock .couponRow .couponBtn:active {
  transform: translateY(1px);
}

.infoList {
  margin: 0;
  padding: 16px 0;
  border-bottom: 1px solid #e5e7eb;
}
.infoList .infoItem {
  display: grid;
  grid-template-columns: 90px 1fr;
  gap: 10px;
  padding: 10px 0;
}
.infoList .infoItem dt {
  font-size: 13px;
  color: #6b7280;
}
.infoList .infoItem dd {
  margin: 0;
  font-size: 13px;
  color: #111827;
  line-height: 1.4;
}
.infoList .infoItem .infoStrong {
  color: #5f0080;
  font-weight: 800;
}
.infoList .infoItem .infoDesc {
  margin: 4px 0 0;
  color: #6b7280;
  font-size: 12px;
}

.optionArea {
  padding: 16px 0 0;
}
.optionArea .optionRow {
  display: grid;
  grid-template-columns: 90px 1fr;
  gap: 10px;
  align-items: center;
}
.optionArea .optionRow .optionLabel {
  font-size: 13px;
  color: #6b7280;
}
.optionArea .optionRow .optionSelect {
  width: 100%;
  height: 42px;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  padding: 0 12px;
  font-size: 14px;
  background: #fff;
  outline: none;
}
.optionArea .optionRow .optionSelect:focus {
  border-color: rgba(95, 0, 128, 0.6);
  box-shadow: 0 0 0 3px rgba(95, 0, 128, 0.12);
}
.optionArea .selectedList {
  margin-top: 12px;
  display: grid;
  gap: 10px;
}
.optionArea .selectedCard {
  border: 1px solid #e5e7eb;
  background: #f9fafb;
  border-radius: 10px;
  padding: 12px;
}
.optionArea .selectedCard .cardTop {
  display: flex;
  align-items: start;
  justify-content: space-between;
  gap: 8px;
}
.optionArea .selectedCard .cardTop .cardTitle {
  margin: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.3;
}
.optionArea .selectedCard .cardTop .cardRemove {
  width: 28px;
  height: 28px;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #6b7280;
  font-size: 18px;
  cursor: pointer;
}
.optionArea .selectedCard .cardTop .cardRemove:hover {
  color: #111827;
  border-color: #d1d5db;
}
.optionArea .selectedCard .cardBottom {
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}
.optionArea .selectedCard .numBox {
  display: inline-flex;
  align-items: center;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  overflow: hidden;
  background: #fff;
}
.optionArea .selectedCard .numBox .numBtn {
  width: 36px;
  height: 34px;
  border: 0;
  background: #fff;
  cursor: pointer;
  font-size: 18px;
  color: #111827;
}
.optionArea .selectedCard .numBox .numBtn:disabled {
  cursor: not-allowed;
  color: #cbd5e1;
}
.optionArea .selectedCard .numBox .numNum {
  width: 42px;
  height: 34px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 14px;
  border-left: 1px solid #e5e7eb;
  border-right: 1px solid #e5e7eb;
}
.optionArea .selectedCard .priceSide {
  text-align: right;
}
.optionArea .selectedCard .priceSide .cardOrigin {
  display: block;
  color: #9ca3af;
  font-size: 12px;
}
.optionArea .selectedCard .priceSide .cardSale {
  display: block;
  font-size: 16px;
  font-weight: 900;
  letter-spacing: -0.2px;
}

.stickyBar {
  position: sticky;
  bottom: 0;
  margin-top: 18px;
  padding-top: 12px;
  background: linear-gradient(to top, #fff 70%, rgba(255, 255, 255, 0));
}

.stickyInner {
  border: 1px solid #e5e7eb;
  background: #fff;
  border-radius: 12px;
  padding: 12px;
  display: grid;
  gap: 10px;
  box-shadow: 0 6px 20px rgba(17, 24, 39, 0.06);
}
.stickyInner .totalLine {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  gap: 10px;
}
.stickyInner .totalLine .totalLabel {
  color: #6b7280;
  font-size: 13px;
}
.stickyInner .totalLine .totalPrice {
  font-size: 20px;
  font-weight: 900;
  letter-spacing: -0.3px;
}
.stickyInner .cartBtn {
  height: 48px;
  border-radius: 10px;
  border: 0;
  background: #5f0080;
  color: #fff;
  font-weight: 800;
  font-size: 15px;
  cursor: pointer;
}
.stickyInner .cartBtn:disabled {
  background: #c4a7cf;
  cursor: not-allowed;
}
.stickyInner .cartBtn:active:not(:disabled) {
  transform: translateY(1px);
}/*# sourceMappingURL=Kully.css.map */