* {
  margin: 0;
  padding: 0;
}

.header {
  background: rgb(0, 149, 194);
  text-align: center;
  position: fixed;
  width: 100%;
}

.title2 {
  color: #02c4ff;
  line-height: 30px;
  margin: 14px 0px;
}

.title3 {
  background: #adfaff;
  margin: 14px 0px;
}

.title4 {
  line-height: 50px;
}

.header>h1 {
  color: #fff;
  font-size: 30px;
}

.gnb {
  position: fixed;
  top: 0px;
  right: -100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2000;
  width: 100%;
  height: 100vh;
  transition: 0.3s;
}

.gnb.open {
  right: 0;
}

.gnb_list {
  background-color: #fff;
  width: 50%;
  height: 100vh;
  padding-top: 60px;
  right: 0px;
  position: absolute;
  list-style: none;

}

.gnb_list>li {
  width: 100%;
  left: 50%;
  border-bottom: 1px solid #02c4ff;
}

.gnb_btn {
  text-align: left;
  width: 90%;
  border: none;
  transition: 0.5s;
  background: none;
  font-size: 20px;
  padding: 5px 10px;
  cursor: pointer;
}

.gnb_btn:hover {
  border-radius: 6px;
  background-color: rgba(2, 196, 255, 0.279);
}

.closeBtn {
  position: absolute;
  right: 20px;
  top: 3px;
  font-size: 36px;
  color: #000000;
  z-index: 1000;
  border: none;
  background: none;
}

main {
  padding: 30px 10px;
}

.list01 {
  margin: 10px 0px 15px 30px;
}

.btn {
  width: 30px;
  height: 30px;
  font-size: 22px;
  line-height: 30px;
  margin: 10px;
  border: 1px solid #02c4ff;
  background-color: #adfaff;
}

.num {
  color: rgb(0, 60, 78);
  font-weight: bold;
  font-size: 30px;
}

footer {
  text-align: center;
  line-height: 40px;
  background: #333;
}

.top_btn {
  position: fixed;
  right: 20px;
  bottom: 100px;
  padding: 14px 12px;
  font-size: 16px;
  background: #fff;
  color: #333;
  border: 1px solid #ccc;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.397);
  cursor: pointer;
  z-index: 500;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s ease, visibility 0.4s ease;
}

.show {
  opacity: 1;
  visibility: visible;
}

.bars {
  font-size: 36px;
  color: #fff;
  position: fixed;
  top: 3px;
  right: 20px;
  cursor: pointer;
  border: none;
  background: none;
  z-index: 1000;

}

footer>address {
  color: #fff;
  background: #333;
}