$kurly: #5f0080;
$line: #e5e7eb;
$muted: #6b7280;
$text: #111827;
$bg: #ffffff;
$card: #f9fafb;

.kurlyDetail {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  background: $bg;
  color: $text;
  border: 1px solid $line;
  border-radius: 12px;
  padding: 18px 18px 88px; // sticky 공간 확보
  box-sizing: border-box;

  * {
    box-sizing: border-box;
  }
}
.productDetailWrap {
  display: grid;
  grid-template-columns: 420px 1fr;
  gap: 32px;
  align-items: start;
}

.imageBox {
  top: 24px;
  .image {
  width: 100%;
  border-radius: 12px;
  object-fit: cover;
  display: block;
  }
}


.pdHeader {
  .pdCategory {
    display: flex;
    align-items: center;
    gap: 8px;
    margin: 0 0 10px;
    font-size: 12px;
    color: $muted;

    .sep {
      opacity: 0.6;
    }
  }

  .pdTitle {
    margin: 0 0 6px;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.25;
    letter-spacing: -0.3px;
  }

  .pdSub {
    margin: 0 0 14px;
    font-size: 13px;
    color: $muted;
  }
}

.priceBlock {
  padding: 14px 0 16px;
  border-top: 1px solid $line;
  border-bottom: 1px solid $line;

  .priceRow {
    display: flex;
    align-items: baseline;
    gap: 10px;
    margin-bottom: 10px;

    .badge {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      height: 22px;
      padding: 0 8px;
      border-radius: 999px;
      background: rgba($kurly, 0.12);
      color: $kurly;
      font-weight: 700;
      font-size: 12px;
    }

    .sale {
      font-size: 24px;
      font-weight: 800;
      letter-spacing: -0.4px;
    }

    .origin {
      color: #9ca3af;
      font-size: 13px;
    }
  }

  .couponRow {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-wrap: wrap;

    .badge.outline {
      background: transparent;
      border: 1px solid rgba($kurly, 0.35);
      color: $kurly;
    }

    .coupon {
      font-size: 18px;
      font-weight: 800;
    }

    .hint {
      font-size: 12px;
      color: $muted;
    }

    .couponBtn {
      margin-left: auto;
      height: 34px;
      padding: 0 12px;
      border-radius: 8px;
      border: 1px solid rgba($kurly, 0.35);
      background: #fff;
      color: $kurly;
      font-weight: 700;
      cursor: pointer;

      &:hover {
        border-color: $kurly;
      }
      &:active {
        transform: translateY(1px);
      }
    }
  }
}

.infoList {
  margin: 0;
  padding: 16px 0;
  border-bottom: 1px solid $line;

  .infoItem {
    display: grid;
    grid-template-columns: 90px 1fr;
    gap: 10px;
    padding: 10px 0;

    dt {
      font-size: 13px;
      color: $muted;
    }

    dd {
      margin: 0;
      font-size: 13px;
      color: $text;
      line-height: 1.4;
    }

    .infoStrong {
      color: $kurly;
      font-weight: 800;
    }

    .infoDesc {
      margin: 4px 0 0;
      color: $muted;
      font-size: 12px;
    }
  }
}

.optionArea {
  padding: 16px 0 0;

  .optionRow {
    display: grid;
    grid-template-columns: 90px 1fr;
    gap: 10px;
    align-items: center;

    .optionLabel {
      font-size: 13px;
      color: $muted;
    }

    .optionSelect {
      width: 100%;
      height: 42px;
      border-radius: 8px;
      border: 1px solid $line;
      padding: 0 12px;
      font-size: 14px;
      background: #fff;
      outline: none;

      &:focus {
        border-color: rgba($kurly, 0.6);
        box-shadow: 0 0 0 3px rgba($kurly, 0.12);
      }
    }
  }

  .selectedList {
    margin-top: 12px;
    display: grid;
    gap: 10px;
  }

  .selectedCard {
    border: 1px solid $line;
    background: $card;
    border-radius: 10px;
    padding: 12px;

    .cardTop {
      display: flex;
      align-items: start;
      justify-content: space-between;
      gap: 8px;

      .cardTitle {
        margin: 0;
        font-size: 14px;
        font-weight: 700;
        line-height: 1.3;
      }

      .cardRemove {
        width: 28px;
        height: 28px;
        border-radius: 8px;
        border: 1px solid $line;
        background: #fff;
        color: #6b7280;
        font-size: 18px;
        cursor: pointer;

        &:hover {
          color: $text;
          border-color: #d1d5db;
        }
      }
    }

    .cardBottom {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 12px;
    }

    .numBox {
      display: inline-flex;
      align-items: center;
      border: 1px solid $line;
      border-radius: 8px;
      overflow: hidden;
      background: #fff;

      .numBtn {
        width: 36px;
        height: 34px;
        border: 0;
        background: #fff;
        cursor: pointer;
        font-size: 18px;
        color: $text;

        &:disabled {
          cursor: not-allowed;
          color: #cbd5e1;
        }
      }

      .numNum {
        width: 42px;
        height: 34px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-weight: 800;
        font-size: 14px;
        border-left: 1px solid $line;
        border-right: 1px solid $line;
      }
    }

    .priceSide {
      text-align: right;

      .cardOrigin {
        display: block;
        color: #9ca3af;
        font-size: 12px;
      }

      .cardSale {
        display: block;
        font-size: 16px;
        font-weight: 900;
        letter-spacing: -0.2px;
      }
    }
  }
}

.stickyBar {
  position: sticky;
  bottom: 0;
  margin-top: 18px;
  padding-top: 12px;
  background: linear-gradient(to top, #fff 70%, rgba(255, 255, 255, 0));
}

.stickyInner {
  border: 1px solid $line;
  background: #fff;
  border-radius: 12px;
  padding: 12px;
  display: grid;
  gap: 10px;
  box-shadow: 0 6px 20px rgba(17, 24, 39, 0.06);

  .totalLine {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    gap: 10px;

    .totalLabel {
      color: $muted;
      font-size: 13px;
    }

    .totalPrice {
      font-size: 20px;
      font-weight: 900;
      letter-spacing: -0.3px;
    }
  }

  .cartBtn {
    height: 48px;
    border-radius: 10px;
    border: 0;
    background: $kurly;
    color: #fff;
    font-weight: 800;
    font-size: 15px;
    cursor: pointer;

    &:disabled {
      background: #c4a7cf;
      cursor: not-allowed;
    }

    &:active:not(:disabled) {
      transform: translateY(1px);
    }
  }
}